<template>
  <div>
    <v-container>
      <v-row class="text-left">
        <v-col>
          <h2>Editar una nota</h2>
        </v-col>
      </v-row>
      <v-row>
        <v-col v-if="alert.flag">
          <Alert :type="alert.type" :message="alert.message" />
        </v-col>
      </v-row>
      <v-row>
        <v-col>
          <v-form @submit.prevent="editOneNote(note)">
            <v-text-field
              :counter="40"
              :rules="titleRules"
              label="Ttulo"
              required
              v-model="note.title"
            ></v-text-field>
            <br />
            <v-textarea outlined label="Descripcin de la tarea" v-model="note.description"></v-textarea>
          </v-form>
        </v-col>
      </v-row>
      <v-row>
        <v-col cols="6" md="2">
          <v-btn type="submit" large color="primary" @click="editOneNote(note)">Actualizar</v-btn>
        </v-col>
        <v-col cols="6" md="2">
          <v-btn type="submit" large color="secondary" @click="emptyOneNote()">Cancelar</v-btn>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
import { mapActions, mapState } from "vuex";

export default {
  name: "EditarNotas",
  data() {
    return {
      titleRules: [
        v => !!v || "Ttulo obligatorio",
        v =>
          (v && v.length <= 40) ||
          "El ttulo no debe ser superior a 40 caracteres"
      ]
    };
  },
  computed: {
    ...mapState("notas", ["note", "alert"])
  },
  methods: {
    ...mapActions("notas", ["editOneNote", "emptyOneNote"])
  }
};
</script>