<template>
  <div>
    <v-container>
      <v-row>
        <v-col>
          <CrearNotas v-if="!flagForm" />
          <EditarNotas v-else />
        </v-col>
      </v-row>
      <v-row class="text-center">
        <v-col>
          <h1>NOTAS</h1>
        </v-col>
      </v-row>
      <v-row>
        <v-col>
          <template v-if="notes.length > 0">
            <v-data-table :headers="headers" :items="notes" class="elevation-1">
              <template v-slot:item.edit="{ item }">
                <v-btn large color="primary" @click="getOneNote(item._id)">EDITAR</v-btn>
              </template>
              <template v-slot:item.delete="{ item }">
                <v-btn large color="accent" @click="deleteOneNote(item._id)">ELIMINAR</v-btn>
              </template>
            </v-data-table>
          </template>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template >
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
// Store
import { mapActions, mapState } from "vuex";

// Components
import CrearNotas from "@/components/CrearNotas.vue";
import EditarNotas from "@/components/EditarNotas.vue";

export default {
  name: "Notas",
  components: {
    CrearNotas, EditarNotas
  },
  data() {
    return {
      headers: [
        { text: "Nombre", align: "start", sortable: true, value: "title" },
        { text: "Descripcin", value: "description" },
        { text: "", value: "edit" },
        { text: "", value: "delete" }
      ]
    };
  },
  computed: {
    ...mapState("notas", ["notes", "note", "flagForm"])
  },
  methods: {
    ...mapActions("notas", ["getNotes", "getOneNote", "deleteOneNote"]),
  },
  created() {
    this.getNotes();
  }
};
</script>