<template>
  <div>
    <v-container>
      <v-row class="text-left">
        <v-col>
          <h1>Hola {{userData.data.nombre}}</h1>
          <h2>Crea una nueva nota</h2>
        </v-col>
      </v-row>
      <v-row>
        <v-col v-if="alert.flag">
          <Alert :type="alert.type" :message="alert.message" />
        </v-col>
      </v-row>
      <v-row>
        <v-col>
          <v-form @submit.prevent="setNote(nota)">
            <v-text-field
              :counter="40"
              :rules="titleRules"
              label="Ttulo"
              required
              v-model="nota.title"
            ></v-text-field>
            <br />
            <v-textarea outlined label="Descripcin de la tarea" v-model="nota.description"></v-textarea>
          </v-form>
        </v-col>
      </v-row>
      <v-row>
        <v-col cols="6" md="2">
          <v-btn type="submit" large color="primary" @click="setNote(nota)">ENVIAR</v-btn>
        </v-col>
        <v-col cols="6" md="2">
          <v-btn type="submit" large color="secondary" @click="cancelNote()">Cancelar</v-btn>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
import Alert from "@/components/Alert.vue";

import { mapActions, mapState } from "vuex";

export default {
  name: "CrearNotas",
  components: {
    Alert
  },
  data() {
    return {
      titleRules: [
        v => !!v || "Ttulo obligatorio",
        v =>
          (v && v.length <= 40) ||
          "El ttulo no debe ser superior a 40 caracteres"
      ],
      nota: {
        title: "",
        description: ""
      }
    };
  },
  computed: {
    ...mapState("users", ["userData"]),
    ...mapState("notas", ["alert"])
  },
  methods: {
    ...mapActions("notas", ["createNote"]),
    setNote(nota) {
      this.createNote(nota);
      setTimeout(() => {
        this.cancelNote();
      }, 100);
    },
    cancelNote() {
      this.nota.title = "";
      this.nota.description = "";
    }
  }
};
</script>